$prefix: '.rise-blocks';
$color-black: #000;

.wp-block-rise-blocks-heading{
    text-align: center;

    &.left {
        text-align: left;
    }
    
    &.right {
        text-align: right;
    }
    
    &.center {
        text-align: center;
    }

    #{$prefix}-sub-title {
        color: $color-black;
        text-transform: capitalize;
        font-weight: 500;
        font-size: 0.7rem;
        border: 1px solid $color-black;
        padding: 0 0.4rem;
        line-height: 1.3rem;
        border-radius: 0.2rem;
        display: inline-block;
        min-width: 30px;
    }

    #{$prefix}-title {  
        padding: 0.4rem 0 0;
        font-size: 2rem;
        font-family: lato;
        text-transform: uppercase;
        margin-top: 0;
        margin-bottom: 0;
        font-weight: 700;
        color: #000;
        line-height: 1.2;

        &:before{
            display: none;
        }
    }

    #{$prefix}-sub-description{
        padding-top: 15px;
        color: #666;
        font-size: 16px;
    }

    #{$prefix}-line{  
        display: inline-block;
        height: 3px;
        width: 50px;
        border-radius: 25%;
        background-color: $color-black;
    }

}

span.rise-blocks-title {
    display: block;
}